<template>
  <div class="flex w-48 mx-auto bg-navy-400 rounded overflow-hidden mb-4">
    <button type="button" @click="select('byMonthly')" class="uppercase text-xs py-1 text-center w-1/2 font-bold tracking-wider rounded focus:outline-none"
    :class="[ isByMonthly ? 'bg-white text-navy-500' : 'text-navy-200 hover:bg-navy-300 hover:text-white' ]">
      Monthly
    </button>
    <button type="button" @click="select('byYearly')" class="uppercase text-xs py-1 text-center w-1/2 font-bold tracking-wider rounded focus:outline-none"
    :class="[ isByYearly ? 'bg-white text-navy-500' : 'text-navy-200 hover:bg-navy-300 hover:text-white' ]">
      Yearly
    </button>              
  </div>
</template>

<script>
export default {
  props: [
    'selected',
  ],
  computed: {
    isByMonthly() {
      return this.selected === 'byMonthly'
    },
    isByYearly() {
      return this.selected === 'byYearly'
    }
  },
  methods: {
    select(period) {
      this.$emit('input', period)
    }
  }
}
</script>
